<template>
  <div class="actions">
    <h3 @click="showDetailHandler">{{ project.title }}</h3>
    <div class="icons">
      <router-link :to="{ name: 'UpdateProject', params: { id: project.id } }"
        ><span class="material-icons-outlined"> edit </span></router-link
      >
      <span @click="deleteHandler(project.id)" class="material-icons-outlined">
        delete
      </span>
      <span @click="doneHandler(project)" class="material-icons-outlined tick">
        done
      </span>
    </div>
  </div>
  <p v-if="showDetail">{{ project.details }}</p>
</template>
<script>
export default {
  props: ["project"],
  emits: ["delete", "done"],
  data() {
    return {
      showDetail: false,
    };
  },
  methods: {
    showDetailHandler() {
      this.showDetail = !this.showDetail;
    },
    deleteHandler(id) {
      this.$emit("delete", id);
    },
    doneHandler(project) {
      this.$emit("done", project);
    },
  },
};
</script>
<style>
.project h3 {
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.project h3::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -10px;
  width: 100%;
  height: 3px;
  background-color: crimson;
  transform: scaleX(0);
  transform-origin: right;
  transition: transform 0.25s ease-in-out;
}

.project h3:hover::before {
  transform: scaleX(1);
  transform-origin: left;
}

.project .actions {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

span.material-icons-outlined {
  font-size: 24px;
  margin-left: 10px;
  color: #bbb;
  cursor: pointer;
}

span.material-icons-outlined:hover {
  color: #777;
}

.project.done h3 {
  text-decoration: line-through;
  color: #bbb;
  pointer-events: none;
}

.project.done .tick {
  color: #08a710;
}
</style>
