<template>
  <div class="filter-nav">
    <button :class="{ active: current === 'all' }" @click="updateFilter('all')">
      View All
    </button>
    <button
      :class="{ active: current === 'completed' }"
      @click="updateFilter('completed')"
    >
      Completed
    </button>
    <button
      :class="{ active: current === 'ongoing' }"
      @click="updateFilter('ongoing')"
    >
      Ongoing
    </button>
  </div>
</template>
<script>
export default {
  methods: {
    updateFilter(by) {
      this.$emit("filter", by);
    },
  },
  emits: ["filter"],
  props: ["current"],
};
</script>
<style scoped>
.filter-nav button {
  background-color: none;
  border: none;
  color: #bbb;
  outline: none;
  font-size: 12px;
  text-transform: uppercase;
  margin-right: 10px;
  letter-spacing: 1px;
  font-weight: bold;
  cursor: pointer;
}

.active {
  color: green !important;
}
</style>
