<template>
  <FilterNav @filter="handleFilter((current = $event))" :current="current" />
  <div class="home">
    <div class="projects">
      <ul class="project-container" v-if="projects || projects?.length">
        <li
          :class="[project.isCompleted ? `project done` : `project not-done`]"
          v-for="project in filteredProject"
          :key="project.id"
        >
          <SingleProject
            @done="doneHandler"
            @delete="deleteHandler"
            :project="project"
          />
        </li>
      </ul>
      <div v-else>There's no project please add one</div>
      <div v-if="isLoadingProjects">{{ isFailedFetchProjects }}</div>
      <div v-if="isLoadingProjects">Loading Project</div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import SingleProject from "../components/SingleProject.vue";
import FilterNav from "@/components/FilterNav.vue";
export default {
  name: "Home",
  components: {
    SingleProject,
    FilterNav,
  },
  data() {
    return {
      projects: [],
      isLoadingProjects: false,
      isFailedFetchProjects: null,
      detail: null,
      current: "all",
    };
  },
  mounted() {
    console.log("component mounted");
    (async () => {
      try {
        this.isLoadingProjects = true;
        const res = await fetch("http://localhost:3000/projects");
        if (res.ok) {
          this.isLoadingProjects = null;
          const data = await res.json();
          this.isLoadingProjects = false;
          this.projects = data;
        } else {
          this.isFailedFetchProjects = "There is a Problem Fetching the data";
        }
      } catch (err) {
        console.log(err);
      }
    })();
  },
  methods: {
    async deleteHandler(id) {
      const response = await fetch("http://localhost:3000/projects/" + id, {
        method: "DELETE",
      });

      if (response.ok) {
        this.projects = this.projects.filter((project) => project.id !== id);
      }
    },
    async doneHandler(project) {
      const response = await fetch(
        "http://localhost:3000/projects/" + project.id,
        {
          method: "PATCH",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify({
            isCompleted: !project.isCompleted,
          }),
        }
      );
      if (response.ok) {
        const p = this.projects.find(
          (currProject) => currProject.id === project.id
        );
        p.isCompleted = !p.isCompleted;
      }
    },
    handleFilter() {},
  },
  computed: {
    filteredProject(val) {
      if (this.current === "completed") {
        return this.projects.filter((project) => project.isCompleted);
      } else if (this.current === "ongoing") {
        return this.projects.filter((project) => !project.isCompleted);
      } else {
        return this.projects.filter((project) => project);
      }
    },
  },
};
</script>

<style scoped>
.project-container {
  padding: 0;
}
.project.done {
  margin: 20px auto;
  background-color: #fff;
  padding: 10px 20px;
  border-radius: 4px;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  border-left: 4px solid #08a710;
  list-style-type: none;
}
.project.not-done {
  margin: 20px auto;
  background-color: #fff;
  padding: 10px 20px;
  border-radius: 4px;
  box-shadow: 1px 2px 3px rgba(0, 0, 0, 0.5);
  border-left: 4px solid #e90074;
  list-style-type: none;
}
</style>
